'use client';
import { useState, useEffect, useCallback } from 'react';

// Типы призов
type Prize = { 
  name: string; 
  type: 'rare' | 'common'; // редкий или обычный
};

// Все возможные призы
const ALL_PRIZES: Prize[] = [
  // Редкие призы (малый шанс)
  { name: 'Онлайн-разбор с Иваном', type: 'rare' },
  { name: 'Приоритетное место у Ивана', type: 'rare' },
  { name: 'Розыгрыш завтрака с Иваном', type: 'rare' },
  { name: 'Ответ Ивана голосом', type: 'rare' },
  { name: 'Звонок 1 на 1 с Антоном', type: 'rare' },
  
  // Обычные призы (хороший шанс)
  { name: '3 чек-листа', type: 'common' },
  { name: 'Созвон с Антоном и БА', type: 'common' },
  { name: '1000 A+', type: 'common' },
  { name: 'Разбор резюме', type: 'common' },
  { name: '500 A+', type: 'common' },
  { name: 'Консультация 15 мин', type: 'common' },
  { name: 'Доступ к материалам', type: 'common' },
  { name: 'Персональная рекомендация', type: 'common' }
];

export default function ShopPage() {
  const [userPluses, setUserPluses] = useState(2);
  const [isSpinning, setIsSpinning] = useState(false);
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [winningPrize, setWinningPrize] = useState<Prize | null>(null);
  const [spinKey, setSpinKey] = useState(0);
  const [slots, setSlots] = useState<string[]>(['', '', '']);

  // Функция для получения случайного приза с учетом вероятности
  const getRandomPrize = useCallback((): Prize => {
    const random = Math.random();
    // 20% шанс на редкий приз, 80% на обычный
    const availablePrizes = random < 0.2 
      ? ALL_PRIZES.filter(p => p.type === 'rare')
      : ALL_PRIZES.filter(p => p.type === 'common');
    
    return availablePrizes[Math.floor(Math.random() * availablePrizes.length)];
  }, []);

  // Анимация прокрутки слотов
  const animateSlots = useCallback((finalPrize: Prize) => {
    const prizeIndex = ALL_PRIZES.findIndex(p => p.name === finalPrize.name);
    const prizePositions = [
      (prizeIndex - 1 + ALL_PRIZES.length) % ALL_PRIZES.length,
      prizeIndex,
      (prizeIndex + 1) % ALL_PRIZES.length
    ];

    // Анимация промежуточных состояний
    let iteration = 0;
    const maxIterations = 20;
    const interval = setInterval(() => {
      iteration++;
      
      if (iteration < maxIterations) {
        // Быстрая прокрутка случайных значений
        setSlots([
          ALL_PRIZES[Math.floor(Math.random() * ALL_PRIZES.length)].name,
          ALL_PRIZES[Math.floor(Math.random() * ALL_PRIZES.length)].name,
          ALL_PRIZES[Math.floor(Math.random() * ALL_PRIZES.length)].name
        ]);
      } else {
        // Финальные значения
        setSlots(prizePositions.map(pos => ALL_PRIZES[pos].name));
        clearInterval(interval);
        setIsSpinning(false);
        
        // Показать выигрыш
        setTimeout(() => {
          setWinningPrize(finalPrize);
          alert(`Поздравляем! Вы выиграли: ${finalPrize.name}`);
        }, 500);
      }
    }, 100);
  }, []);

  const handleSpin = () => {
    if (isSpinning || userPluses < 1) return;
    
    setIsSpinning(true);
    setWinningPrize(null);
    setSpinKey(prev => prev + 1);
    
    // Определяем выигрыш
    const prize = getRandomPrize();
    
    // Запускаем анимацию
    animateSlots(prize);
    
    // Обновляем баланс
    setUserPluses(prev => prev - 1);
  };

  // Инициализация начальных значений слотов
  useEffect(() => {
    setSlots([
      ALL_PRIZES[0].name,
      ALL_PRIZES[1].name,
      ALL_PRIZES[2].name
    ]);
  }, []);

  return (
    <div className="flex flex-col min-h-screen font-sans items-center px-4 pt-6 pb-4 text-center text-black bg-gray-50">

      {/* Заголовок магазина */}
      <div className="w-full mb-6">
        <h1 className="text-2xl font-bold mb-2">
          Магазин
        </h1>
        <p className="text-gray-600">
          Обменивай свои плюсы на интересные товары!
        </p>
      </div>
      
      {/* Блок с балансом */}
      <div className="w-full bg-white rounded-xl shadow-sm p-5 mb-5">
        <div className="text-gray-700 mb-4">
          У вас доступно <span className="font-semibold">{userPluses} плюсов</span>
        </div>
      </div>

      {/* Горизонтальный слот с анимацией */}
      <div className="w-full bg-white rounded-xl shadow-sm p-4 mb-5">
        <div className="flex justify-center items-center h-32 mb-4 bg-gradient-to-br from-purple-100 to-blue-100 rounded-lg overflow-hidden border-2 border-purple-200">
          <div className="flex space-x-3">
            {slots.map((slot, index) => (
              <div 
                key={`${spinKey}-${index}`}
                className="w-28 h-28 flex items-center justify-center bg-white border-2 border-purple-300 rounded-xl shadow-inner text-xs font-medium p-2 text-center transition-all duration-200 transform hover:scale-105"
              >
                {slot}
              </div>
            ))}
          </div>
        </div>
        
        <button 
          onClick={handleSpin}
          disabled={isSpinning || userPluses < 1}
          className="w-full h-14 flex items-center justify-center bg-gradient-to-r from-purple-600 to-blue-500 text-white text-lg font-bold rounded-xl 
          transition-all
          shadow-[0_4px_0_0_rgba(91,33,182,0.6)] 
          active:translate-y-1 active:shadow-[0_1px_0_0_rgba(91,33,182,0.6)]
          disabled:opacity-50 disabled:cursor-not-allowed"
        >
          {isSpinning ? 'Крутится...' : 'Крутить'}
        </button>
        
        <div className="text-sm text-purple-600 font-medium mt-2">
          Крутить стоит 1 плюс
        </div>
      </div>

      {/* Товар - созвон */}
      <div className="w-full bg-white rounded-xl shadow-sm p-5">
        <h2 className="text-lg font-bold mb-4">Премиум товар</h2>
        
        <div className="flex justify-between items-center py-3">
          <div className="text-left">
            <div className="font-medium">Созвон с кумиром</div>
            <div className="text-sm text-gray-500">30 минут личного общения</div>
          </div>
          <div className="flex items-center">
            <span className="text-purple-600 font-bold mr-3">10,000 плюсов</span>
            <button 
              disabled={userPluses < 10000}
              className="bg-purple-600 text-white text-sm font-semibold px-3 py-1 rounded-lg disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Купить
            </button>
          </div>
        </div>
      </div>

    </div>
  );
}